// Variables //

:root {
    --breakpoint-xs: 0;
    --breakpoint-sm: 576px;
    --breakpoint-md: 768px;
    --breakpoint-lg: 992px;
    --breakpoint-xl: 1200px;
}

html.dark {
    --color-0: #000;
    --color-1: #111;
    --color-2: #222;
    --color-3: #333;
    --color-4: #444;
    --color-5: #555;
    --color-6: #666;
    --color-7: #777;
    --color-8: #888;
    --color-9: #999;
    --color-a: #aaa;
    --color-b: #bbb;
    --color-c: #ccc;
    --color-d: #ddd;
    --color-e: #eee;
    --color-f: #fff;

    --color-fons: var(--color-0);
    --color-fons-card: var(--color-2);
    --color-fons-card-2: var(--color-3);
    --color-fons-card-modal: #222d;
    --color-tab-obert: var(--color-4);
    --border-card: 1px solid var(--color-4);
    --color-gray: var(--color-8);
    --color-text: var(--color-f);
    --color-text-link: #8ab4f8;
    --color-text-error: #ff84a3;
    --color-text-success: #84ff84;

    --border-input: 2px solid var(--color-8);

    --color-fons-toast: var(--color-3);
    --color-fons-toast-success: #009655;
    --color-fons-toast-danger: #a4003d;
    --color-fons-toast-info: #208aff;
    --color-fons-toast-warning: #977800;

    color-scheme: dark;
}
html.light {

    --color-0: #fff;
    --color-1: #eee;
    --color-2: #ddd;
    --color-3: #ccc;
    --color-4: #bbb;
    --color-5: #aaa;
    --color-6: #999;
    --color-7: #888;
    --color-8: #777;
    --color-9: #666;
    --color-a: #555;
    --color-b: #444;
    --color-c: #333;
    --color-d: #222;
    --color-e: #111;
    --color-f: #000;

    --color-fons: var(--color-1);
    --color-fons-card: var(--color-0);
    --color-fons-card-2: var(--color-2);
    --color-fons-card-modal: #fffd;
    --color-tab-obert: var(--color-2);
    --border-card: none;
    --color-gray: var(--color-8);
    --color-text: var(--color-d);
    --color-text-link: #3481ff;
    --color-text-error: #ca0022;
    --color-text-success: #00ca00;

    --border-input: 2px solid var(--color-8);

    --color-fons-toast: var(--color-fons-card-user);
    --color-fons-toast-success: #d7ffaa;
    --color-fons-toast-danger: #ffb9b3;
    --color-fons-toast-info: #bbdcff;
    --color-fons-toast-warning: #fff3bb;
}

// PC //
@media only screen and (min-width: 576px) {
    :root {
        --appbar-altura: 80px;
    }
}
// Mòbil //
@media only screen and (max-width: 576px) {
    :root {
        --appbar-altura: 50px;
    }
}
