.loader-wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  background-color: #fff;
  z-index: 11;
  top: 0;
  /*Color Loader*/
  /*Color Loader*/
}
.loader-wrapper .loader-index {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  filter: url("#goo");
}
.loader-wrapper .loader-index:after {
  content: "";
  width: 4em;
  height: 4em;
  background: var(--theme-deafult);
  border-radius: 50%;
  position: absolute;
  transform: scale(0.5);
  animation: grow 2s cubic-bezier(0.14, 0.05, 0.55, 0.5) infinite alternate;
}
.loader-wrapper .loader-index span {
  width: 2em;
  height: 2em;
  background: var(--theme-secondary);
  border-radius: 50%;
  margin-right: 2em;
  position: relative;
  transform: translateX(7em);
  animation: move 4s ease-in-out infinite;
}
.loader-wrapper .loader-index span:before {
  content: "";
  width: 2em;
  height: 2em;
  background: var(--theme-deafult);
  border-radius: 50%;
  position: absolute;
  left: 3em;
  transform: translateX(0em);
  animation: shrink 2s ease-in-out infinite;
}
.loader-wrapper .loader-index span:after {
  content: "";
  width: 2em;
  height: 2em;
  background: #a927f9;
  border-radius: 50%;
  position: absolute;
  right: 3em;
  transform: translateX(0em);
  animation: shrink 2s ease-in-out infinite;
}
@keyframes grow {
  0% {
    transform: scale(0.5);
    background: #a927f9;
  }
  50% {
    transform: scale(1);
    background: var(--theme-secondary);
  }
}
@keyframes move {
  0% {
    transform: translateX(7em);
  }
  50% {
    transform: translateX(-5em);
  }
}
@keyframes shrink {
  0% {
    transform: translateX(0em);
  }
  50% {
    transform: translateX(-1em);
  }
}
.loader-wrapper svg {
  width: 0;
  height: 0;
}
.loader-wrapper body {
  font-family: sans;
  min-height: 100vh;
  margin: 0;
  display: grid;
  place-content: center;
  background: black;
}
.loader-wrapper h1 {
  color: #fff;
  text-align: center;
  margin-bottom: 2em;
  letter-spacing: 3px;
  position: relative;
  z-index: 2;
}
.loader-wrapper h1:before {
  position: absolute;
  content: "";
  height: 1px;
  background: linear-gradient(to right, var(--theme-secondary), var(--theme-deafult), #a927f9);
  color: #fff;
  top: 2.75em;
  left: 0.75em;
  width: 2.5em;
  font-weight: 400;
  font-variant: small-caps;
  font-size: 0.5em;
}

.loader-box {
  height: 150px;
  text-align: center;
  display: flex;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
  transition: 0.3s color, 0.3s border, 0.3s transform, 0.3s opacity;
}
.loader-box [class*=loader-] {
  display: inline-block;
  width: 50px;
  height: 50px;
  color: inherit;
  vertical-align: middle;
}
.loader-box .loader-1 {
  border: 0.2em dotted var(--theme-deafult);
  border-radius: 50%;
  animation: 1s loader-01 linear infinite;
}
@keyframes loader-01 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-2 {
  border: 0.2em solid transparent;
  border-left-color: var(--theme-secondary);
  border-right-color: var(--theme-secondary);
  border-radius: 50%;
  animation: 1s loader-02 linear infinite;
}
@keyframes loader-02 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-3 {
  border: 0.2em solid #a927f9;
  border-bottom-color: transparent;
  border-radius: 50%;
  animation: 1s loader-03 linear infinite;
  position: relative;
}
@keyframes loader-03 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-4 {
  border: 1px solid #a927f9;
  border-radius: 50%;
  animation: 1s loader-04 linear infinite;
  position: relative;
}
.loader-box .loader-4:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  top: -0.2em;
  left: 50%;
  border: 0.2em solid #a927f9;
  border-radius: 50%;
}
@keyframes loader-04 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-5 {
  border: 0.2em solid transparent;
  border-top-color: #f8d62b;
  border-radius: 50%;
  animation: 1s loader-05 linear infinite;
  position: relative;
}
.loader-box .loader-5:before {
  content: "";
  display: block;
  width: inherit;
  height: inherit;
  position: absolute;
  top: -0.2em;
  left: -0.2em;
  border: 0.2em solid #f8d62b;
  border-radius: 50%;
  opacity: 0.5;
}
@keyframes loader-05 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-box .loader-6 {
  border: 0.2em solid #dc3545;
  border-radius: 50%;
  animation: loader-06 1s ease-out infinite;
}
@keyframes loader-06 {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
.loader-box .loader-7 {
  border: 0 solid transparent;
  border-radius: 50%;
  position: relative;
}
.loader-box .loader-7:before, .loader-box .loader-7:after {
  content: "";
  border: 0.2em solid var(--theme-deafult);
  border-radius: 50%;
  width: inherit;
  height: inherit;
  position: absolute;
